import React, { useEffect, useState } from "react";
import { useHistory } from "react-router";
import Products from "./Products";
import axios from "axios";
import "../Assets/Card.css"
import { Toast,Row,Col,Button,ToastContainer } from "react-bootstrap";
import LandingPage from "./LandingPage";
import { useSelector } from "react-redux";
const Home = () => {
  const [show, setShow] = useState(false)
  const [Data, setData] = useState([])
  const [Cart, setCart] = useState("")
  const [OldCart, setOldCart] = useState("")
  const [position, setPosition] = useState('bottom-end');
  const  userLoginId=localStorage.getItem("userLogin")
  const history = useHistory()
  const selector = useSelector(state => state.searchReducer)
  const catSelector = useSelector(state => state.getProductsReducer)

 console.log(catSelector);

useEffect(() => {

    axios.get("http://localhost:4000/products")
      .then((response) => setData(response.data.data)      
           );
          
  
    }, []) 

    
    const addToCart = (id) =>{
    
      if (!userLoginId) {
        history.push("/login");
      }
      axios.post("http://localhost:4000/checkcart",{
        userid:userLoginId,
        productid:id,
        quantity:0
           })
      .then((response) =>{ 


        if (!response.data.success) {
          
          axios.post(`http://localhost:4000/cart/`,{
         
            userid:userLoginId,
                productid:id,
                quantity:0
            
          }).then(()=>{
      
            setShow(true)
          })
        }
        else{
          alert("already in cart")

        }
      setOldCart(response.data.success)
    
    }
      );
        
            // console.log(Cart);

            
            
          }

     
      

return (
<div  >
<LandingPage/>
{catSelector?<h1>|{catSelector}</h1>:<h1>|Products</h1>}
<div className="d-flex  flex-wrap"  > 

  
{
  Data.filter((val) => {
    // console.log(selector);
    if (selector == "") {
      // console.log(search);
      return val;
    } else if (
      val.name.toLowerCase().includes(selector.toLowerCase())
    ) {
      
      // console.log(search.toLowerCase);
      return val;
    }
  }).map((dd,i)=>{
    
    if (dd.category==catSelector) {
    
    return (
      
            
          <div>
        <div  className="card text-center p-1 bg-light" id="cardId">
  {/* {catSelector}          */}
          <div className="py-2">
            <img src={dd.img} 
            alt="" style={{width:"100%",height:"20vh"}} />
        </div>
        <div className="uppercase">
            {dd.name}
        </div>
        <div className="descr mx-auto my-1">
            {dd.descr}
        </div>
        <div className="d-flex mx-auto">
            <h2 className="productPrice">{dd.price}</h2> <h2>₹</h2>
        </div>
        
        <div>
            
           
        </div>
      

        
        
        <div className="d-flex  align-items-center ">
            <button className="btn btn-dark rounded-pill mx-2 my-2" onClick={()=>{
              setCart(dd._id)
              addToCart(dd._id)
            }}>
                <span>wishlist </span>
                <i className="fa fa-heart  text-danger " aria-hidden="true"></i>
            </button>
            
            <button className="btn-danger btn rounded-pill mx-3"  onClick={()=>{
              history.push("/productDetails", {'id': dd._id});
            }}>Buy Now </button>
        </div>
        
      </div>
      </div>
)}
else if(catSelector==""){

  return (
    
       <div>
           

      <div  className="card text-center p-1 bg-light" id="cardId">
         
        <div className="py-2">
          <img src={dd.img} 
          alt="" style={{width:"10vw",height:"20vh"}} />
      </div>
      <div className="uppercase">
          {dd.name}
      </div>
      <div className="descr mx-auto my-1">
          {dd.descr}
      </div>
      <div className="d-flex mx-auto">
          <h2 className="productPrice">{dd.price}</h2> <h2>₹</h2>
      </div>
      
      <div>
          
         
      </div>
    

      
      
      <div className="d-flex  align-items-center ">
          <button className="btn btn-dark rounded-pill mx-2 my-2" onClick={()=>{
            setCart(dd._id)
            addToCart(dd._id)
          }}>
              <span>wishlist </span>
              <i className="fa fa-heart  text-danger " aria-hidden="true"></i>
          </button>
          
          <button className="btn-danger btn rounded-pill mx-3"  onClick={()=>{
            history.push("/productDetails", {'id': dd._id});
          }}>Buy Now </button>
      </div>
      
    </div>
    </div>
)
}


   
  })
}

</div>

<Row>
<div style={{position:'relative',bottom:'0rem'}}>
      <Col xs={6}>
     <ToastContainer position={position} className="m-5 position-absolute">
     <Toast position={position} onClose={() => setShow(false)} show={show} delay={3000} autohide>
          <Toast.Header>
            <img
              src="holder.js/20x20?text=%20"
              className="rounded me-2"
              alt=""
              />
            <strong className="me-auto">PrCart</strong>
          </Toast.Header>
          <Toast.Body>Item Added To Cart 
            <i class="fa fa-cart-plus text-primary" aria-hidden="true"></i>
            </Toast.Body>
        </Toast>
     </ToastContainer>
      </Col>
</div>
      
    </Row>
  

</div>     
)
}


export default Home;
